.total-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.card-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    padding: 20px;
}

@media (max-width: 768px){
    .card-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 450px) {
    .total-container {
        grid-template-columns: 1fr;
    }
    .card-container {
        grid-template-columns: 1fr;
    }
}